$h-text:white;
$highlight:rgb(36, 133, 36);
$backgound:rgb(204, 204, 204); 
$fade-text:rgb(128, 128, 128);
$card-background:rgb(68, 68, 68);

$h-font:monospace;

*{
    margin: 0;
    padding: 0;
}
nav{
    background-color: rgb(48, 46, 46);
    padding:10px;

    h2{
        color:$h-text;
        font-family: $h-font
    }
}

.home-body{
    background-color: $backgound;
    padding: 10px;
    

.section-container{
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    justify-content: space-around;
    
    
    h2{
     color:$highlight;  
     padding: 10px; 
    }

    
    hr{
        border-style: none none solid none;
        border-width: 1px;
        width: 75%;
        margin: 10px;
    }
    .section{
        display: flex;
        flex-wrap: wrap;
        flex-direction: row;
        justify-content: space-around;
        align-items: center;

        .video-card{
            display:flex;
            flex-direction: column;
            background-color:$card-background ;
            width: 40%;
            overflow: hidden;
            margin: 10px;
            border-radius: 10px;
            
            img{
                width: 100%;
                

            }
            
            .video-details{
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                color: $fade-text;
                padding: 10px;
                h4{
                    color: $h-text;
                    
                    font-family: $h-font;
                }

                .card-footer{
                    display: flex;
                    flex-direction: row;
                    justify-content: space-between;

                    p{
                        font-size:small;
                        
                    }
                }
                
                
            }
        } 
    }
}
    
    
}

